export default {
	data() {
		return {
			tel: "",
			pwd: "",
			pwdShow: false
		};
	},
	computed: {
	},
	methods: {
		ShowPass(){
			this.pwdShow = !this.pwdShow
		},
		LoginMsg(){
			uni.redirectTo({
				url: '../login-verify/login-verify'
			})
		},
		ForgetPwd(){
			uni.navigateTo({
				url: '../login-forgetpwd/login-forgetpwd'
			})
		},
		Login(){
			if(!(/^[1][3,4,5,7,8][0-9]{9}$/.test(this.tel))){
				uni.showToast({
					icon: "none",
				    title: '请输入正确手机号',
				    duration: 2000
				});
			} else {
				if(this.pwd != ''){
					uni.reLaunch({
						url: "../index/index"
					})
				} else {
					uni.showToast({
						icon: "none",
					    title: '请输入密码',
					    duration: 2000
					});
				}
			}
			
		},
		ClearTel(){
			this.tel = ''
		},
		ViewIntro(){
			uni.navi
		},
	}
}
